@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css);
* {
  margin: 0;
  padding: 0;
  
}
*:focus{
  outline:none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px !important;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-padding: 3rem;
  line-height: 1.5;
}

.app-container{
    max-width: 1200px;
    margin: auto;
}
.Tripzone {
    max-width: 1900px;
    margin: auto;
  }
.header-container {
  padding: 1.5rem;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(19, 51, 80, 1) 100%
  );
  color: #fff;
  text-align: center;
}
.header-container .header-main__title{
    margin-bottom: 0.5rem;
    font-weight: 300;
}
.hero-container .hero-content__block-title {
  font-size: 2.6rem;
    margin-bottom: 1rem;
    font-weight: 900;
  }
  .hero-container .hero-content__block p {
      font-size: 1.5rem;
      font-weight: 400;
      font-family: 'Roboto';
      width: 40%;
  }
  .hero-container .hero-content__block h4{
    font-size: 1.1rem;
    font-weight: 400;
    color: #343333;
  }
  .hero-container .hero-image__block-image {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

@media (min-width:1024px) {
  .hero-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin: 2rem  auto;
  }
  .hero-container .hero-image__block {
    width: 40%;
    padding-left:  4rem;
  }

  .hero-container .hero-content__block {
    width: 40%;
    margin-right: 2rem;
  }
  
}
@media (max-width:1023px) {
  .hero-container{
    padding: 2rem;
    text-align: center;
  }
  .hero-container .hero-image__block-image {
    margin-bottom: 2rem;
  }
  .hero-container .hero-content__block h4 {
    font-size: 2rem;
    max-width: 85%;
    margin: auto;
  }
}

.button-primary{
    background: #000;
    background: linear-gradient( 
    90deg
    , #000 0%, #133350 100% );
    color: #fff;
    padding: 0.8rem 2rem;
    font-size: 1rem;
    font-weight: bold;
    margin: 2rem 0;
    border: 0;
    border-radius: 5px;
    text-transform:uppercase;
    cursor: pointer;
    display: inline-block;
    text-decoration: none;
}
.nav-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}
.nav-container .nav-links__group {
  display: -webkit-flex;
  display: flex;
  max-width: 1300px;
  list-style: none;
}
.nav-container .nav-links__item {
  margin: 0 1rem;
}
.nav-container .nav-links__item .nav-links__item-link {
  color: #000;
  text-decoration: none;
  font-weight: 500;
}
@media (max-width:767px) {
  .nav-container {
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
}
.slider-container {
  margin-left: 5vw;
  padding: 0px 10px;
}
.slider-container img {
  width: 185px;
  height: 175px;
  object-fit: cover;
  border-radius: 10px;
  display: block;
  border-bottom-left-radius: 0;
  box-shadow: 1px 1px 2px 1px #dddddd3d;
}
.slider-container h4 {
  color: #aac3a7;
  margin: 1rem 0;
}
.slider-container h2 {
  color: #133357;
  margin-bottom: 1.5rem;
}

.slide-item {
  padding-right: 17px;
  min-width: 200px !important;
}
@media (max-width: 768px) {
  .slider-container img {
    width: 90%;
  }

  .slick-initialized .slick-slide {
    display: block;
    max-width: 206px;
  }
}

@media (max-width: 480px) {
  .slider-container img {
    width: 100%;
  }
}

.quote-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: auto;
  max-width: 500px;
  width: 100%;
}

.quote-container h2 {
  text-align: center;
  margin: 0 0 2rem 0;
  color: #133357;
}
.quote-container form {
  width: 100%;
}
.quote-container form button {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 1.5rem auto 1rem auto;
}
.quote-container .quote-form__group {
  margin-bottom: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.quote-container .quote-form__group .quote-form__label {
  margin-bottom: 0.5rem;
  color: #aac3a7;
  font-weight: 500;
  font-size: 1rem;
}
.quote-container .quote-form__group .quote-form__input {
  border-radius: 5px;
  height: 2.8rem;
  border: 1.8px solid #aac3a7;
  padding: 1rem;
}
.quote-container .quote-form__group .quote-form__error {
  color: red;
  margin: 0.5rem 0;
  display: none;
}
.quote-container .quote-form__success{
  background-color: #d0f8cc;
  color: #aac3a7;
  padding: 1rem;
  border-top: 1px solid #ced5cd;
  border-bottom: 1px solid #ced5cd;
}
.quote-form__success:empty{
  display: none;
}

@media (max-width: 1023px) {
  .quote-container {
    max-width: 350px;
  }
}

.weather-container {
  color: #ffffff;
  padding: 1rem 0;
  margin-bottom: 2rem;
}

.weather-container .weather-heading {
  background-color: #133357;
  padding: 1rem;
}

.weather-container .weather-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.weather-container .weather-body .weather-status {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: auto;
          flex: auto;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.weather-container .weather-body img {
  width: 4rem;
  display: block;
  height: 3rem;
  margin-bottom: 0.8rem;
}

.weather-container .weather-body .weather-status__temparature {
  font-weight: 400;
  font-size: 4.5rem;
  margin-bottom: 1rem;
}
.weather-container .weather-body .weather-status__cities {
  margin: 0.8rem;
  font-size: 1.5rem;
}

@media only screen and (max-width: 768px) {
  .weather-container {
    padding: 0 2rem;
  }
  .weather-container .weather-heading {
    text-align: center;
  }
  .weather-container .weather-body {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .weather-container .weather-body .weather-status {
    width: 50%;
  }
 
  .weather-container .weather-body .weather-status__temparature {
    font-size: 3.5rem;
  }
}

.promo {
  position: relative;
  color: #fff;
  margin: 2rem 0;
  font-weight: bold;
}
.promo img {
  width: 100%;
  display: block;
  height: auto;
}
.promo .promo-content {
  position: absolute;
  margin: auto;
  -webkit-transform: translate(7%, -2rem);
          transform: translate(7%, -2rem);
  bottom: 0;
}

.promo .promo-content .promo-heading {
  font-size: 3.5rem;
  margin-bottom: 10px;
  font-weight: 500;
}

.promo .promo-content .promo-body {
  font-size: 2.05rem;
  margin-top: 1rem;
}

@media only screen and (max-width: 1023px) {
  .promo .promo-content .promo-heading {
    font-size: 20px;
  }

  .promo .promo-content .promo-body {
    font-size: 18px;
  }
  .promo .promo-content{
    left: 1rem;
  }
}
@media (min-width: 1024px) {
  .promo .promo-content {
    -webkit-transform: translate(20%, -2rem);
            transform: translate(20%, -2rem);

  }
}

.footer-container {
  background: #000;
  background: linear-gradient(90deg, #000 0%, #133350 100%);
  padding: 2rem;
  margin-top: 1rem;
}
.footer-container > .footer-section {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.footer-container ul {
  list-style: none;
  padding: 0;
}

.footer-container ul li {
  padding: 10px 0;
}
.footer-container * {
  color: #ffffff;
  text-decoration: none;
}

.footer-container h2 {
  font-weight: 500;
  margin-bottom: 1.5rem;
}
.footer-container a {
  font-size: 1.2rem;
  font-weight: 400;
}
.footer-container p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.7rem;
}
.footer-container .footer-col {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
}
.footer-container .footer-col:last-of-type {
  -webkit-flex-grow: 2;
          flex-grow: 2;
}
@media only screen and (max-width: 767px) {
  .footer-container .footer-section {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .footer-container .footer-col:not(:last-of-type) {
    margin-bottom: 4rem;
  }
}

@media (min-width: 1200px) {
  .footer-container .footer-section {
    width: 1200px;
  }
}

@media (max-width: 1023px) {
  .footer-container .footer-col {
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
  }
}

